export function throttle(throttledFunction: (...args) => void, delay: number) {
  let isThrottling = false;

  return function (...args: any[]) {
    if (!isThrottling) {
      throttledFunction(...args);
      isThrottling = true;
      setTimeout(function () {
        isThrottling = false;
      }, delay);
    }
  };
}
